@import url(https://fonts.googleapis.com/css2?family=Shippori+Mincho+B1&display=swap);
@tailwind base;
@tailwind components;
@tailwind utilities;
* {
  padding: 0;
  margin: 0;
  background-color: #dddddd;
}

.title {
  font-family: 'Shippori Mincho B1', serif;
  text-align: center;
  font-size: 50px;
  padding-top: 10px;
  padding-bottom: 20px;
  background-color: transparent;
}

h2,
em {
  font-family: 'Shippori Mincho B1', serif;
  text-align: center;
  background-color: transparent;
}

.header {
  background-color: #334;
}

.title {
  color: white;
  font-size: 6em;
  font-weight: 100;
}

.subtitle {
  background-color: #446;
  border-top: 0.1px solid white;
  border-bottom: 0.1px solid white;
  padding: 20px;
  margin-bottom: 10px;
  color: white;
}

.text {
  font-weight: 100;
}
* {
    margin: 0;
    padding: 0;
}

.placeholder {
    margin: 40px 20% 0 20%;
    display: grid;
    grid-template-rows: 3fr 1fr;
    grid-template-areas: 
    'box box box box box box box box box box box box box box box box box'
    'submit submit submit submit submit submit submit submit submit submit submit submit submit submit submit submit submit'
    ;
}

.textbox {
    padding-bottom: 100px;
    align-self: center;
    grid-area: box;
    background-color: #339;
    resize: none;
}

.textbox:focus {
    background-color: #33b;
    color: white;
}


.clear:hover {
    background-color: #222;
}

.clear:active {
    background-color: #333;
}

.submit {
    background-color: #334;
    color: white;
    font-size: large;
    grid-area: submit;
    border: none;
}

.submit:hover {
    background-color: #33a;
}

.submit:active {
    background-color: #33f;
}
.post {
    background-color: #334;
    color: white;
    padding: 30px;
    padding-bottom: 10px;
    margin-left: 20%;
    margin-right: 20%;
    margin-top: 40px;
    margin-bottom: 50px;
    border-radius: 10px;
}

.post:hover {
    cursor: pointer;
    border: 3px solid #338;
}

.btn {
    width: 30px;
    height: 30px;
    display: block;
    margin: 0 0 20px -20px;
    border: none;
    border-radius: 7px;
}

.up {
    margin-top: -50px;
    margin-bottom: 10px;
    background-color: #008b8b;
}

.up:hover {
    background-color: #009b9b;
}

.down {
    margin-top: 10px;
    background-color: #fa8072;
}

.down:hover {
    background-color: #fd9082;
}

.votes {
    margin: 0;
    background-color: transparent;
    font-size: 28px;
    margin-left: -12px;
}
* {
    background-color: #44a;
}
