* {
    margin: 0;
    padding: 0;
}

.placeholder {
    margin: 40px 20% 0 20%;
    display: grid;
    grid-template-rows: 3fr 1fr;
    grid-template-areas: 
    'box box box box box box box box box box box box box box box box box'
    'submit submit submit submit submit submit submit submit submit submit submit submit submit submit submit submit submit'
    ;
}

.textbox {
    padding-bottom: 100px;
    align-self: center;
    grid-area: box;
    background-color: #339;
    resize: none;
}

.textbox:focus {
    background-color: #33b;
    color: white;
}


.clear:hover {
    background-color: #222;
}

.clear:active {
    background-color: #333;
}

.submit {
    background-color: #334;
    color: white;
    font-size: large;
    grid-area: submit;
    border: none;
}

.submit:hover {
    background-color: #33a;
}

.submit:active {
    background-color: #33f;
}